body{
  margin:0;
}
.mainPage{
  
  background-image: url('../components/images/lio.jpg');
  background-repeat: no-repeat;
  
  background-position: bottom right;
  height:100vh;
  width: 100vw;

}
.card {
  background-color:rgba(15, 214, 237, .8  );
  position: relative;
  top: 25vh;
  width:30vw;
  height:15vh;
  border: 5px solid white;
 margin: 0px 20px;
 text-align: center;

 font-size: 40px;
}
.card p{
  margin:0;
  font-size: 15px;
  color: black;
}
.available{
  color: white;
  float:left;
}
.unAvailable{
  color: red;
  float:left;
}
.contact{
  float:right;
  color: white;
}
.contactDetails{
  /* display: inline-flex; */
  /* flex-direction: row; */
  position: absolute;
  top:40vh;
  right:1vw;
}
.contactDetails a{
  color: white;
  font-size: 30px;
  margin:5px;
  text-decoration: none;
}
