body{
    margin:0;
}
.About {
    height: 100vh;
    width:100vw;
    /* display: inline-flex;
    justify-content: space-between; */
    background-position: fixed;
    background-image: url('../components/images/bridge.jpg');
    background-size:cover;
    background-repeat: no-repeat;
}
.banner{
    background-color: black;
    position:sticky;
    width:100%;
    color:white;
    font-weight: bold;
    margin:0;
    /* top:0; */
    padding:50px 0px;
    z-index: 0;
    font-size:30px;
    border-bottom: 1px solid white;
}
.caption{
    position: sticky;
    z-index:5;
    /* top:47px; */
    text-align: center;
    background:rgba(0, 0, 0, .99);
    color:white;
    height:50px;
    margin:0;
    padding:15px 0px;
    font-size: 20px;
 
}
.closeView{
    margin:0;
  
     position:relative;
     z-index: 6;
     top:1vh;
     left:1vw;
     font-size: 30px;
     float:left;
  
    padding:3px;
     color:darkred;
     
}

.title p{
    display: flex;
  margin:40px 100px;
    text-align: center;

  
}
.title:hover{
    text-shadow: 2px 2px 30px burlywood;
}
.titleCol{
    /* float:left; */
    border-top: 1px solid grey;
    border-bottom: 1px solid grey;
    color:white;
    font-size:20px;
    display:flex;
  flex-direction: row;
width:100%;
overflow-x: scroll;
position: relative;
top:10vh;
    /* padding: 2vh 10vw 2vh 10vw; */
    background-color:black;
  
   
   
}
.detailedContent{
    
    overflow:scroll;

}
.content{
    animation: contentBox 1s;  
   justify-self: center;
    border: 3px solid black;
    height:80vh;
    width:60vw;
    position: relative;
    top:10vh;
    left:20vw;
    color:white;
    font-size: 20px;
    text-align: center;
    background-color: black;

}
/* .topListItem{
    margin-top: 250px;
} */
.detailedContent img{
    height:300px;
    margin:50px;
}


.homeLink {
    animation-name: homeLink ;
    animation-duration: 2s;
    animation-iteration-count: 1;
    color:#9FBE2C;
    position: absolute;
   width:100vw;
   height:10vh;
   background-color: black;
    top:0; 
    left:0;
    padding:30px 0px 10px 20px;
    font-size: 50px;
    text-decoration: none;

}
.homeLink:hover{
    color:black;
    font-weight: bold;
    text-shadow: 0px 0px 5px #D5FE3D;
}
.rightArrow, .leftArrow{
    color:darkgrey;
    margin-top: 15vh;
    font-size: 20px;
    position: sticky;
   z-index: 5;
   
}
.rightArrow{
    right:40vw;
}
.leftArrow{

   left:40vw;

}

@keyframes homeLink{
    0%{
        margin-left: 100vw;
        background-color: unset;
        width:max-content;
    }
    70%{
        width:min-content;
        transform: rotate(90deg);
        transform: rotateY(180deg);
        text-shadow: 10px 10px 10px black;
        background-color: unset;
        
    }
    100%{
    
       margin-left: 0vw;
  
    } 
}
@keyframes contentBox{
    from{margin-top: -100vh;
        

}
    to{margin-top:0;
       
     
    }
}


  

/* HOVER EMOJIS */
.pizzaEmoji, .iceCreamEmoji, .puzzlesEmoji, .tacosEmoji, .familyEmoji, .raceCarEmoji, .craftsEmoji {
display:none;

}
 .pizza:hover .pizzaEmoji, .iceCream:hover .iceCreamEmoji, .puzzles:hover .puzzlesEmoji, .tacos:hover .tacosEmoji, .family:hover .familyEmoji, .raceCar:hover .raceCarEmoji, .crafts:hover .craftsEmoji {  
  display:inline-block;
  
}
